.container {
	background-color:beige;
	margin: 20px 20px;
	
	.header{
		background-color:grey;
	}
}




/* Load custom font */
@font-face {
  font-family: "myfont";
  src: url("../fonts/NB.otf");
}

/* Apply custom font */
body{
  font-family: "myfont";
  font-size: 22px;
}

/* Vertical span a */
span.a {
  display: inline-block;
  height: 200px;
}

/* Vertical span b */
span.b{
  display: inline-block;
  height: 50px;
}

/* Float left align text */
.alignleft {
  float: left;
}
/* Float right align text */
.alignright {
  float: right;
}


.current {
  color: green;
}

.navigation-header { 
  color: black;
  font-size:20px;
}

.contact-info { 
   color: black;
   font-size:20px;
}

a:visited {
  color: black;
}

a:link {
  color: blue;
  text-decoration: none; 
}

a:hover {
  color: red;
  text-decoration: underline;
}

/** Navbar **/
/* The navbar container */
.topnav {
  overflow: hidden;
  background-color: white;
}

/* Navbar links */
.topnav a {
  float: right;
  color: black;
  text-align: right;
  padding: 14px;
  padding-left: 0px;
  text-decoration: none;
}

/* Links - change color on hover */
.topnav a:hover {
  color: grey;
}